body {
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.textcontent {
  padding-left: 10px;
  padding-right: 10px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.animate-spin-slow {
  animation: spin 6s linear infinite;
  @keyframes spin {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
}

.navbar {
  background-color: rgb(15 23 42);
}

.navbar-item {
  position: relative;
  display: inline-block;
  padding: 8px 10px;
  border: 3px solid rgb(15 23 42);
  cursor: pointer;
  text-wrap: nowrap;
}

.navbar-item-active {
  background-color: #ffffff;
  border: 3px solid rgb(15 23 42);
  color: black;
}

.navbar-item:hover {
  color: white;
  background: rgb(75 85 99);
}

.navbar-item:hover .dropdown-menu {
  color: white;
  display: block;
}

.navbar-link {
  text-decoration: none;
  padding: 10px 20px;
  z-index: 5;
}

.navbar-link:hover {
  color: white;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 2px;
  background-color: white;
  border: 1px solid #000000;
  box-shadow: 0 8px 16px rgba(97, 96, 96, 0.743);
  z-index: 50;
  min-width: 150px;
  color: black;
  display: none;
}

.dropdown-item {
  display: block;
  padding: 8px 12px;
  text-decoration: none;
  border: 5px black;
  color: black;
  margin-bottom: 3px;
}

.dropdown-item:hover {
  color: white;
  text-decoration: none;
  background-color: rgb(75 85 99);
}

.dropdown-item-active .navmenu-item-active {
  border: 1px solid #333333;
  background-color: rgb(219, 219, 219);
  color: black;
}

.navmenu-item {
  padding: 8px 12px;
  border-radius: 0.375rem;
  color: black;
  background-color: rgb(157, 157, 157);
  cursor: pointer;
}

/* .navmenu-item-active {
  border: 1px solid #333333;
} */

.navmenu-subitem {
  padding: 8px 12px;
  border-radius: 0.375rem;
  color: black;
  background-color: rgb(205, 204, 204);
}

.navmenu-subitem-active {
  border: 3px solid #333333;
}

.navmenu-item:hover,
.navmenu-subitem:hover {
  background-color: rgb(75 85 99);
  color: white;
}

.user-area {
  position: absolute;
  top: 85%;
  padding: 8px 12px;
  background-color: white;
  border: 1px solid #ccc;
  box-shadow: 0 8px 16px rgba(97, 96, 96, 0.743);
  z-index: 1000;
  max-width: 200px;
  min-width: 150px;
  color: black;
  word-wrap: break-word;
  display: flex;
  flex-direction: column;
  justify-items: right;
  gap: 4px;
}

.user-area-info {
  font-size: 10px;
  text-align: right;
  padding-bottom: 12px;
}

.user-area-link {
  background-color: #d2d2d2;
  text-align: right;
  padding: 2px 8px;
  border-radius: 3px;
  cursor: pointer;
  color: inherit
  /* <NavbarItem className="lg:flex bg-white hover:bg-gray-600 pt-0 pb-0 mb-2 pl-2 pr-2 rounded-md"> */
}

.user-area-link:hover {
  background-color: rgb(75 85 99);
  color: white;
  text-decoration: none;
}

.user-area-loading {
  background-color: (15 23 42);
}

.input-profile {
  margin: 0 5px;
  flex: 1 0 auto;
  min-height: 25px;
  max-height: 25px;
  min-width: 200px;
  font-size: small;
  padding: 0 2px 0 5px;
  border: none;
  border-bottom: 2px solid rgb(0, 0, 0);
  border-color: rgb(157, 157, 157);
}

.input-profile:focus {
  outline: none;
}

.tr-profile {
