  justify-content: center;
  text-align: left;
  flex-wrap: nowrap;
}

.product-details-row {
  display: flex;
  flex-wrap: nowrap;
}

.product-details-row strong {
  flex: 0 1 auto;
  vertical-align: bottom;
}

.product-details-row input,
select {
