}

.button-standard {
  padding: 6px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
  vertical-align: bottom;
}

.button-standard:hover, button-standard-inline:hover {
  background-color: #406992;
}

.button-standard-blue-grey {
  padding: 6px 20px;
  border: none;
  border-radius: 5px;
  background-color: #8ba6b6;
  color: white;
  cursor: pointer;
