  background-color: #007bff;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
  vertical-align: bottom;
}

.button-new {
  height: 32px;
  width: 120px;
  padding: 6px 10px;
  border: 2px solid #000000;
  border-radius: 5px;
  background-color: #003c7d;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
  vertical-align: bottom;
}

.button-new:hover {
  background-color: #406992;
}

.button-disabled {
  background-color: #808080;
}

.button-disabled:hover {
  background-color: #808080;
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

.button-pagination {
  margin-right: 4px;
  padding: 5px 10px;
